.modal {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;

  overflow: auto;

  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.3);
}

.modal__wrapper {
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;

  margin: 55px auto;
  position: relative;
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;

  cursor: pointer;
  font-size: 46px;
}

